
import {
  toRefs,
  PropType,
  reactive,
  defineComponent,
  ComponentPublicInstance,
} from 'vue';
import type {Locale} from 'date-fns';
import {enGB} from 'date-fns/locale';
import usei18n from '../../../composables/usei18n';
import Icon from '@ohrm/oxd/core/components/Icon/Icon.vue';
import Input from '@ohrm/oxd/core/components/Input/Input.vue';
import {formatDate, parseDate, freshDate} from '../../../utils/date';
import Calendar from '@ohrm/oxd/core/components/Calendar/Calendar.vue';

export default defineComponent({
  name: 'OxdDateInput',

  components: {
    'oxd-icon': Icon,
    'oxd-input': Input,
    'oxd-calendar': Calendar,
  },

  inheritAttrs: false,

  props: {
    modelValue: {
      type: String,
      required: false,
      default: null,
    },
    hasError: {
      type: Boolean,
      required: false,
      default: false,
    },
    disabled: {
      type: Boolean,
      required: false,
      default: false,
    },
    readonly: {
      type: Boolean,
      required: false,
      default: false,
    },
    placeholder: {
      type: String,
      required: false,
      default: null,
    },
    ioformat: {
      type: String,
      required: false,
      default: 'yyyy-MM-dd',
    },
    displayFormat: {
      type: String,
      required: false,
      default: null,
    },
    value: {
      type: String,
      required: false,
      default: null,
    },
    locale: {
      type: Object as PropType<Locale>,
      required: false,
      default: () => enGB,
    },
  },

  emits: [
    'blur',
    'update:modelValue',
    'dateselect:opened',
    'dateselect:closed',
  ],

  setup() {
    const state = reactive<{open: boolean; dateTyped: string | null}>({
      open: false,
      dateTyped: null,
    });

    return {
      ...usei18n(),
      ...toRefs(state),
    };
  },

  computed: {
    dateSelected: {
      get() {
        return parseDate(this.modelValue, this.ioformat) || undefined;
      },
      set(value: Date) {
        if (!isNaN(value?.valueOf()) && value instanceof Date) {
          this.$emit('update:modelValue', formatDate(value, this.ioformat));
        } else {
          this.$emit(
            'update:modelValue',
            this.dateTyped ? this.dateTyped : null,
          );
        }
      },
    },
    displayDate(): string | null {
      if (this.value) {
        return this.value;
      } else if (this.dateSelected) {
        return formatDate(this.dateSelected, this.dateFormat, {
          locale: this.locale,
        });
      } else if (this.modelValue === null) {
        return this.modelValue;
      } else {
        return this.dateTyped;
      }
    },
    dateFormat(): string {
      return this.displayFormat && this.displayFormat.trim()
        ? this.displayFormat
        : this.ioformat;
    },
    dateIconClasses(): object {
      return {
        'oxd-date-input-icon': true,
        '--disabled': this.disabled,
        '--readonly': this.readonly,
      };
    },
  },

  methods: {
    onBlur(e: Event) {
      e.stopImmediatePropagation();
      if (this.dateTyped !== null) {
        this.dateSelected =
          parseDate(this.dateTyped, this.dateFormat, {
            locale: this.locale,
          }) || undefined;
      }
      this.$emit('blur');
      this.closeDropdown();
    },
    onDateTyped(value: string) {
      this.dateTyped = value;
    },
    onDateSelected() {
      this.dateTyped = null;
      this.closeDropdown();
    },
    toggleDropdown() {
      if (!this.disabled && !this.readonly) {
        if (!this.open) {
          this.openDropdown();
          (this.$refs.oxdInput as ComponentPublicInstance).$el.focus();
        } else {
          this.closeDropdown();
        }
      }
    },
    openDropdown() {
      this.open = true;
      this.$emit('dateselect:opened');
    },
    closeDropdown() {
      this.open = false;
      this.$emit('dateselect:closed');
    },
    onClickToday() {
      this.open = false;
      this.dateTyped = null;
      this.dateSelected = freshDate();
    },
    onClickClear() {
      this.open = false;
      this.dateTyped = null;
      this.dateSelected = undefined;
    },
  },
});
