
import {ComponentPublicInstance, defineComponent} from 'vue';
import Icon from '@ohrm/oxd/core/components/Icon/Icon.vue';
import Input from '@ohrm/oxd/core/components/Input/Input.vue';
import {parseDate, formatDate} from '../../../../utils/date';
import clickOutsideDirective from '../../../../directives/click-outside';
import TimePicker from '@ohrm/oxd/core/components/Input/Time/TimePicker.vue';

export default defineComponent({
  name: 'OxdTimeInput',

  components: {
    'oxd-icon': Icon,
    'oxd-input': Input,
    'oxd-time-picker': TimePicker,
  },

  directives: {
    'click-outside': clickOutsideDirective,
  },

  props: {
    modelValue: {
      type: String,
      required: false,
      default: '',
    },
    hasError: {
      type: Boolean,
      required: false,
      default: false,
    },
    disabled: {
      type: Boolean,
      required: false,
      default: false,
    },
    readonly: {
      type: Boolean,
      required: false,
      default: false,
    },
    placeholder: {
      type: String,
      required: false,
      default: null,
    },
    step: {
      type: Number,
      required: false,
      default: 1,
    },
  },

  emits: [
    'blur',
    'update:modelValue',
    'timeselect:opened',
    'timeselect:closed',
  ],

  data() {
    return {
      open: false,
    };
  },

  computed: {
    timeIconClasses(): object {
      return {
        'oxd-time-input--clock': true,
        '--disabled': this.disabled,
        '--readonly': this.readonly,
      };
    },
    timeDisplay(): string | null {
      const parsedDate = parseDate(this.modelValue, 'HH:mm');
      return parsedDate ? formatDate(parsedDate, 'hh:mm a') : null;
    },
  },

  methods: {
    onFocusOut() {
      this.open && this.closeDropdown();
    },
    onTimeInput($event: Event) {
      const input = ($event.target as HTMLInputElement).value;
      const parsedDate = parseDate(input, 'hh:mm a');
      this.$emit(
        'update:modelValue',
        parsedDate ? formatDate(parsedDate, 'HH:mm') : null,
      );
    },
    toggleDropdown() {
      if (!this.disabled) {
        if (!this.open) {
          (this.$refs.oxdInput as ComponentPublicInstance).$el.focus();
          this.openDropdown();
        } else {
          this.closeDropdown();
        }
      }
    },
    openDropdown() {
      this.open = true;
      this.$emit('timeselect:opened');
    },
    closeDropdown() {
      this.open = false;
      this.$emit('timeselect:closed');
    },
  },
});
