
import {defineComponent} from 'vue';
import {Position, LABEL_POSITIONS, RIGHT} from './types';

export interface State {
  focused: boolean;
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  checkedProxy: any;
}

export default defineComponent({
  name: 'OxdRadioInput',

  inheritAttrs: false,

  props: {
    modelValue: {
      type: null,
      required: false,
      default: undefined,
    },
    style: {
      type: Object,
      required: false,
      default: () => ({}),
    },
    hasError: {
      type: Boolean,
      required: false,
      default: false,
    },
    optionLabel: {
      type: String,
      required: false,
      default: '',
    },
    labelPosition: {
      type: String,
      required: false,
      default: RIGHT,
      validator: function (value: Position) {
        return LABEL_POSITIONS.indexOf(value) !== -1;
      },
    },
    disabled: {
      type: Boolean,
      equired: false,
      default: false,
    },
  },

  emits: ['blur', 'focus', 'change', 'update:modelValue'],

  data(): State {
    return {
      focused: false,
      checkedProxy: {},
    };
  },

  computed: {
    classes(): object {
      return {
        'oxd-radio-input': true,
        'oxd-radio-input--active': !this.focused,
        'oxd-radio-input--focus': this.focused,
        'oxd-radio-input--error': this.hasError,
        [`--label-${this.labelPosition}`]: true,
      };
    },
    checked: {
      get() {
        return this.modelValue;
      },
      set(value: unknown) {
        this.checkedProxy = value;
      },
    },
  },

  methods: {
    onFocus(e: Event) {
      this.focused = true;
      this.$emit('focus', e);
    },
    onBlur(e: Event) {
      this.focused = false;
      this.$emit('blur', e);
    },
    onChange(e: Event) {
      this.$emit('update:modelValue', this.checkedProxy);
      this.$emit('change', e);
    },
  },
});
